.section1 {
  background: url(../images/bg2.png);
  background-repeat: repeat-x;
  background-size: cover;
  position: relative;
  width: 100%;
  left: 0;
  right: 0;
  height: 120vh;
  margin-left: 0;
  margin-right: 0;
}
.section1 .bg {
  position: absolute;
  left: 0;
  right: 0;
  height: 120vh;
}
.section1 .bg .figures {
  position: absolute;
  left: -30vw;
  display: flex;
  width: 160vw;
  height: 120vh;
  align-items: center;
  justify-content: space-between;
  scale: 0.8125;
}
.section1 .bg .figures *:nth-child(1) {
  position: relative;
  top: 100px;
}
.section1 .bg .figures *:nth-child(2) {
  position: relative;
  top: -70px;
}
.section1 .bg .circles {
  position: absolute;
  width: 92vw;
  margin-left: 4vw;
  height: 120vh;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}
.section1 .bg .circles .circle {
  width: 65px;
  position: relative;
  height: 65px;
  scale: 0.8125;
  border-radius: 50%;
  border: 20px solid #FFBB00;
  background: rgba(19, 19, 19, 0);
}
.section1 .bg .circles .circle1 {
  opacity: 0.5;
  top: -120px;
}
.section1 .bg .circles .circle2 {
  opacity: 0.2;
  width: 120px;
  height: 120px;
  top: -20px;
}
.section1 .bg .circles .circle3 {
  opacity: 0.1;
  top: 300px;
}
.section1 .bg .circles .circle4 {
  opacity: 0.2;
  top: -200px;
}
.section1 .bg .circles .circle5 {
  opacity: 0.5;
  top: 200px;
}
.section1 .about-me {
  height: fit-content;
}
.section1 .about-me .content {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: self-start;
  height: 90vh;
  padding: 5vw 12vw;
}
.section1 .about-me .content .info {
  position: relative;
}
.section1 .about-me .content .info p.about {
  color: #FFF;
  font-family: Oxanium;
  font-size: 29px;
  font-weight: 400;
  width: 495px;
}
.section1 .about-me .content .info p.descript {
  color: #FB0;
  font-family: Oxanium;
  font-size: 16.2px;
  font-weight: 400;
}
.section1 .about-me .content .info:nth-child(2) {
  align-self: flex-end;
}
.section1 .about-me .content .info:nth-child(3) {
  right: -10vw;
}
.section1 .about-me .content .info:nth-child(4) {
  right: 5vw;
  align-self: flex-end;
}
.section1 .about-me .content .info:nth-child(5) {
  right: -2vw;
}
@media screen and (max-width: 900px) {
  article.section1 {
    height: 130vh;
  }
  article.section1 section.about-me .content {
    gap: 50px;
  }
}
@media screen and (max-width: 620px) {
  article.section1#section1 {
    height: 140vh;
    min-height: min-content;
  }
  article.section1#section1 section.about-me .content {
    height: 110vh;
    justify-content: center;
    padding: 10vw 0 0 2vw;
  }
  article.section1#section1 section.about-me .content .info p {
    width: 96vw;
  }
  article.section1#section1 section.about-me .content .info:nth-child(2) {
    align-self: flex-start;
  }
  article.section1#section1 section.about-me .content .info:nth-child(3) {
    right: 0vw;
  }
  article.section1#section1 section.about-me .content .info:nth-child(4) {
    right: 0vw;
    align-self: flex-start;
  }
  article.section1#section1 section.about-me .content .info:nth-child(5) {
    right: 0vw;
  }
}
@media screen and (max-width: 418px) {
  article.section1#section1 {
    height: 140vh;
  }
  article.section1#section1 .about-me {
    height: fit-content;
  }
  article.section1#section1 .content {
    height: fit-content;
  }
}
