header {
  position: fixed;
  z-index: 10000;
  width: 90vw;
  height: 70px;
  background: #ffffff08;
  display: flex;
  justify-content: space-between;
  padding: 0 5vw;
  align-items: center;
}
header div.line-scroll {
  background-color: #FFF;
  position: absolute;
  top: 0;
  left: 0;
  height: 5px;
}
header div.i {
  color: #FFF;
  font-family: Open Sans;
  font-size: 16px;
  font-weight: 500;
}
header div.right {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  gap: 10vw;
}
header div.right nav ul {
  list-style: none;
  display: flex;
  gap: 4vw;
}
header div.right nav ul li {
  cursor: pointer;
  color: #FFF;
  font-family: Open Sans;
  font-size: 16px;
  font-weight: 500;
  position: relative;
  top: 0;
  transition: all 0.3s linear;
}
header div.right nav ul li a {
  color: #fff;
  text-decoration: none;
}
header div.right nav ul li::after {
  content: "";
  border-bottom: 2px solid #FB0;
  display: block;
  width: 0px;
  position: absolute;
  bottom: 0;
  transition: all 0.5s 0.1s ease;
}
header div.right nav ul li:hover::after {
  width: 100%;
  left: 0;
}
header div.right nav ul li:hover {
  color: #FB0;
  top: -3px;
}
header div.right nav ul li:hover a {
  color: #FB0;
}
header div.right select {
  color: #FFF;
  font-family: Open Sans;
  font-size: 15px;
  font-weight: 500;
  background: transparent;
  border: none;
  outline: none;
}
header div.right select option {
  background: #131313;
}
@media screen and (max-width: 640px) {
  header nav {
    display: none;
  }
}
