body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #131313;
}
* {
  margin: 0;
  padding: 0;
  &::selection {
    color: rgb(255, 106, 0);
    background-color: #ffffff36;
  }
}
@font-face {
  font-family: Porter;
  src: url('../public/fonts/PorterSansBlock-Regular.ttf');
}
@font-face {
  font-family: Oxanium;
  src: url('../public/fonts/Oxanium-Regular.ttf');
}
@font-face {
  font-family: Open Sans;
  src: url('../public/fonts/OpenSans-Bold.ttf');
}
@font-face {
  font-family: Oxanium-bold;
  src: url('../public/fonts/OpenSans-Bold.ttf');
}
h2.title {
  text-wrap: nowrap;
  color: rgba(255, 255, 255, 0.10);
  font-family: Oxanium;
  font-size: 78px;
  width: 500px;
  position: relative;
  left: -400px;
  font-weight: 300;

}
.section1 h2.title {
  animation: run-about 2s linear infinite;
}
 .section2 h2.title {
  animation: run 2s linear infinite;
}
.section3 h2.title {
  animation: run-work 2s linear infinite;
}
@keyframes run-about {
  from {
    left: -400px;
  }
  to {
    left: 25px;
  }
}
@keyframes run-work {
  from {
    left: -400px;
  }
  to {
    left: 39px;
  }
}
@keyframes run {
  from {
    left: -400px;
  }
  to {
    left: 18px;
  }
}
@media screen and (max-width: 630px) {
  h2.title {
    font-size: 50px;
}
}
body::-webkit-scrollbar {
  width: 12px;
}
body::-webkit-scrollbar-track {
  background: #131313;  
}
body::-webkit-scrollbar-thumb {
  background-color: #fb0;   
  border-radius: 20px; 
  border: 3px solid #131313;  
}