.section2 {
  background: url(../images/bg3.png);
  background-repeat: repeat-x;
  background-size: cover;
  position: relative;
  width: 100%;
  left: 0;
  right: 0;
  height: 120vh;
  min-height: fit-content;
  margin-left: 0;
  margin-right: 0;
}
.section2 .my-skills {
  height: fit-content;
  position: relative;
}
.section2 .my-skills .content {
  height: fit-content;
  margin-top: 30px;
}
.section2 .my-skills .content .row {
  display: flex;
  margin-bottom: 10px;
  justify-content: center;
}
.section2 .my-skills .content .row .skill {
  width: fit-content;
  border-radius: 30px;
  padding: 0 20px;
  border: 2px solid #FFF;
  background: transparent;
  color: #FFF;
  text-align: center;
  font-family: Oxanium;
  font-size: 39px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.section2 .my-skills .content .row:nth-child(1) {
  width: 75vw;
  justify-content: space-around;
}
.section2 .my-skills .content .row:nth-child(2) {
  width: 85vw;
  margin-left: 15vw;
  justify-content: space-around;
}
.section2 .my-skills .content .row:nth-child(3) {
  width: 80vw;
  margin-left: 20vw;
}
.section2 .my-skills .content .row:nth-child(4) {
  width: 90vw;
  margin-bottom: 50px;
  margin-left: 5vw;
  margin-right: 5vw;
  justify-content: space-between;
}
.section2 .my-skills .content .row:nth-child(5) {
  width: 70vw;
  margin-left: 10vw;
  gap: 20px;
  margin-right: 20vw;
  margin-bottom: 70px;
  justify-content: space-between;
}
.section2 .my-skills .content .row:nth-child(6) {
  width: 70vw;
  margin-left: 15vw;
  margin-right: 15vw;
  justify-content: space-between;
}
.section2 .my-skills .content .row:nth-child(7) {
  width: 70vw;
  margin-left: 30vw;
}
.section2 .my-skills .content .row:nth-child(8) {
  display: block;
  width: 90vw;
  margin-left: 5vw;
}
.section2 .my-skills .content .row:nth-child(9) {
  width: 60vw;
  gap: 20px;
  margin-left: 20vw;
  margin-right: 20vw;
  justify-content: space-between;
}
.section2 .my-skills .bottom {
  right: 5vw;
  position: absolute;
  align-content: end;
  justify-content: end;
  color: #FFF;
  text-align: center;
  font-family: Oxanium;
  font-size: 39px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
@media screen and (max-width: 740px) {
  .section2#section2 {
    height: fit-content;
  }
  .section2#section2 .content {
    margin-top: 30px;
  }
  .section2#section2 .row:nth-child(1n) {
    display: flex;
    flex-wrap: wrap;
    width: 95vw;
    margin-left: 2.5vw;
    margin-right: 2.5vw;
    justify-content: space-evenly;
    margin: 0;
    gap: 10px;
  }
  .section2#section2 .row:nth-child(1n) .skill {
    font-size: 30px;
    margin-top: 10px;
    margin-bottom: 20px;
  }
  .section2#section2 .row:nth-child(5) .skill {
    margin-top: 20px;
  }
  .section2#section2 .bottom {
    display: none;
  }
}
