main {
  background: url(../images/bg1.png);
  background-repeat: repeat-x;
  background-size: cover;
  position: relative;
  width: 100%;
  left: 0;
  right: 0;
  height: 120vh;
  margin-left: 0;
  margin-right: 0;
}
main .content {
  position: relative;
  padding: 0 5vw;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: stretch;
  height: 100vh;
}
main .content .left {
  margin-top: 150px;
}
main .content .left h1 {
  text-transform: uppercase;
  color: #FFF;
  font-family: Oxanium;
  font-size: 78px;
  font-style: normal;
  font-weight: 900;
  line-height: normal;
  width: 640px;
}
main .content .left .descript {
  width: 450px;
  color: #FFF;
  font-family: Oxanium;
  font-size: 30px;
  font-style: normal;
  position: relative;
  font-weight: 400;
  line-height: normal;
}
main .content .right {
  position: absolute;
  right: 5vw;
  bottom: 10px;
}
main .content .right p.timer-text {
  color: #FFF;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: Oxanium;
  font-size: 35px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  width: 400px;
}
main .content .right p.timer-text span.timer {
  font-weight: 500;
  animation: timer infinite 1s ease;
}
@keyframes timer {
  from {
    color: #ffc527;
  }
}
@media screen and (max-width: 460px) {
  main .content .left h1 {
    font-size: 60px;
    width: 400px;
  }
  main .content .left .descript {
    width: 350px;
  }
  main .content .right {
    right: 1vw;
  }
  main .content .right p.timer {
    font-size: 30px;
    width: 300px;
  }
}
