footer {
  background: linear-gradient(234deg, rgba(17, 16, 18, 0.2) 0%, rgba(95, 77, 10, 0.2) 100%);
  background-repeat: repeat-x;
  background-size: cover;
  position: relative;
  width: 90vw;
  left: 0;
  right: 0;
  height: 20vh;
  margin-left: 0;
  margin-right: 0;
  padding: 20px 5vw;
  height: fit-content;
}
footer p {
  width: 40vw;
  color: rgba(255, 255, 255, 0.8);
  font-family: Oxanium;
  font-size: 19px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
footer div.bottom {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: flex-end;
}
footer div.bottom p {
  color: rgba(255, 255, 255, 0.5);
  font-family: Oxanium;
  font-size: 19px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
footer div.bottom button {
  background: transparent;
  border-radius: 20px;
  border: 2px solid #FFF;
  color: #FFF;
  text-wrap: nowrap;
  font-family: Oxanium;
  font-size: 48px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  margin-left: 50px;
  margin-top: 20px;
  padding: 0 20px;
  cursor: pointer;
  transition: all 0.3s ease;
}
footer div.bottom button a {
  color: #FFF;
  text-decoration: none;
}
footer div.bottom button:hover {
  border-color: #FB0;
  scale: 0.95;
}
footer div.bottom button:hover a {
  color: #FB0;
}
