.section3 {
  background: url(../images/bg4.png);
  background-repeat: repeat-x;
  background-size: cover;
  position: relative;
  width: 100%;
  left: 0;
  right: 0;
  height: fit-content;
  margin-left: 0;
  margin-right: 0;
}
.section3 .my-works {
  padding-bottom: 200px;
}
.section3 .my-works .content {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 100px;
}
.section3 .my-works .content .work {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.section3 .my-works .content .work .img {
  cursor: pointer;
  overflow: hidden;
  transition: all 0.5s 0.1s ease;
  position: relative;
  z-index: 2;
  height: 185px;
}
.section3 .my-works .content .work .img:hover {
  scale: 0.97;
}
.section3 .my-works .content .work .img:hover img.site {
  scale: 1.1;
  filter: grayscale(0);
}
.section3 .my-works .content .work .img img.site {
  filter: grayscale(0.85);
  width: 520px;
  transition: all 0.5s 0.1s ease;
}
.section3 .my-works .content .work .info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 200px;
  width: 446px;
}
.section3 .my-works .content .work .info h3 {
  color: #FB0;
  font-family: Porter;
  font-size: 52px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.section3 .my-works .content .work .info p.year {
  color: #FFF;
  font-family: Oxanium;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.section3 .my-works .content .work .info:nth-child(odd) {
  text-align: end;
}
.section3 .my-works .content .work:nth-child(1) {
  margin-left: 10vw;
  flex-wrap: wrap;
}
.section3 .my-works .content .work:nth-child(2) {
  align-self: flex-end;
  margin-right: 10vw;
  flex-wrap: wrap-reverse;
}
.section3 .my-works .content .work:nth-child(3) {
  margin-left: 2.5vw;
  flex-wrap: wrap-reverse;
}
.section3 .my-works .content .work:nth-child(4) {
  align-self: flex-end;
  margin-right: 5vw;
}
.section3 .my-works .content .work:nth-child(5) {
  align-self: flex-end;
  margin-right: 15vw;
  flex-wrap: wrap;
}
.section3 .my-works button.all {
  background: transparent;
  border: none;
  color: #FFF;
  font-family: Oxanium;
  font-size: 48px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  position: absolute;
  cursor: pointer;
  left: 30px;
  bottom: 20px;
  height: 60px;
  width: 250px;
  background-size: 300% 100%;
  border-radius: 50px;
  transition: all 0.8s ease-in-out;
  background-image: linear-gradient(to right, #FB0, #e81919c0, #cd5c00, #ebe304c0);
  box-shadow: 0 4px 15px 0 #ffbb00;
  animation: button 5s linear infinite;
}
.section3 .my-works button.all a {
  color: white;
  text-decoration: none;
}
.section3 .my-works button.all:hover {
  background-position: 100% 0;
}
@media screen and (max-width: 1080px) {
  .section3#section3 .my-works .content .work .img img {
    margin-left: 5vw;
    width: 90vw;
  }
  .section3#section3 .my-works .content .work .info {
    justify-content: center;
    width: 98vw;
  }
  .section3#section3 .my-works .content .work .info h3 {
    text-align: center;
  }
  .section3#section3 .my-works .content .work .info p {
    text-align: center;
  }
  .section3#section3 .my-works .content .work:nth-child(1) {
    margin-left: 0vw;
  }
  .section3#section3 .my-works .content .work:nth-child(2) {
    align-self: flex-start;
    margin-right: 0vw;
  }
  .section3#section3 .my-works .content .work:nth-child(3) {
    margin-left: 0vw;
  }
  .section3#section3 .my-works .content .work:nth-child(4) {
    align-self: flex-start;
    margin-right: 0vw;
  }
  .section3#section3 .my-works .content .work:nth-child(5) {
    align-self: flex-start;
    margin-right: 0vw;
  }
  .section3#section3 .my-works button {
    font-size: 24px;
    margin-left: 10px;
  }
}
